.login {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
  h2 {
    margin: 0;
    padding: 10px 0px;
  }
  &__form {
    padding-top: 50px;
    text-align: center;
    &-input {
      padding-top: 10px;
    }
    &-rememberMe {
      font-size: 14px;
      padding-top: 5px;
    }
    &-submit {
      display: flex;
      align-items: center;
      justify-content: center;
      padding-top: 15px;
    }
    &-error {
      display: flex;
      justify-content: center;
      padding-top: 5px;
      font-size: 12px;
      color: red;
    }
  }
}